<template>
    <div class="chat-messages">
        <x-messenger :schema="messenger" @selected-user="selectedUser" @unselected-user="unselectedUser" @send-message="sendMessage"></x-messenger>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'ChatMessages',
    computed: {
        ...mapState('sample', ['messenger']),
    },
    methods: {
        selectedUser(user) {
            this.messenger.window = 1;
        },
        unselectedUser() {
            this.messenger.window = 0;
        },
        async sendMessage(fields) {
            this.$store.commit('sample/SEND_MESSAGE', { text: fields.text.value });

            await this.$sleep(100);
            fields.text.value = null;
        },
    },
}
</script>

